.ai-image-generator{
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    margin-top: 80px;
    margin-bottom: 20px;
    gap: 30px;
}

.header{
    font-size: 70px;
    font-weight: 500;
    padding-bottom: 30px;
}

.header span{
   color: #DE1B89; 
   font-weight: 600;
}

.image {
    width: 35%;
    height: 35%;
  }

  .image img {
    width: 100%;
    height: auto;
  }

  .img-loading{
    display: flex;
    flex-direction: column;
  }

  .img-loading img{
    width: 512px;
  }

  .search-box{
    display: flex;
    width: 900px;
    height: 85px;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    background: #1f3540;
  }

  .search-input{
    width: 500px;
    height: 50px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 18px;
    color: #ffffff;
    padding-left: 35px;
    margin-right:35px;
  }

  .generate-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 75px;
    font-size: 20px;
    border-radius: 50px;
    background: #DE1B89;
    cursor: pointer;
  }